import React, { useState, useEffect } from 'react';
import {
  Container,
  Grid,
  Typography,
  Box,
  Chip,
  Paper,
  CircularProgress,
  Alert,
  ImageList,
  ImageListItem,
  styled,
  Divider,
} from '@mui/material';
import {
  DirectionsCar as CarIcon,
  Home as HomeIcon,
  VerifiedUser as VerifiedIcon,
  AccessTime as TimeIcon,
  SmokingRooms as SmokingIcon,
  SmokeFree as NoSmokingIcon,
  Star as StarIcon,
  Group as GroupIcon,
  Person as PersonIcon,
} from '@mui/icons-material';
import { useParams } from 'react-router-dom';
import { Profile } from '../types';
import { api } from '../utils/api';
import { serviceTranslations } from '../utils/serviceTranslations';

const StyledPaper = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(3),
  marginBottom: theme.spacing(3),
}));

const IconChip = styled(Chip)(({ theme }) => ({
  margin: theme.spacing(0.5),
}));

const ProfileImage = styled('img')({
  width: '100%',
  height: '100%',
  objectFit: 'cover',
});

const ProfilePage: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const [profile, setProfile] = useState<Profile | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchProfile = async () => {
      try {
        setLoading(true);
        setError(null);
        const response = await api.get(`/profiles/${id}`);
        setProfile(response.data);
      } catch (error) {
        console.error('Error fetching profile:', error);
        setError('   ');
      } finally {
        setLoading(false);
      }
    };

    if (id) {
      fetchProfile();
    }
  }, [id]);

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: '80vh' }}>
        <CircularProgress />
      </Box>
    );
  }

  if (error || !profile) {
    return (
      <Container maxWidth="lg" sx={{ mt: 4 }}>
        <Alert severity="error">{error || '  '}</Alert>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Grid container spacing={3}>
        {/*   */}
        <Grid item xs={12}>
          <StyledPaper>
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
              <Typography variant="h4" component="h1">
                {profile.name}, {profile.age}
              </Typography>
              <Chip
                icon={<VerifiedIcon />}
                label={profile.isVerified ? "" : " "}
                color={profile.isVerified ? "success" : "warning"}
                sx={{ ml: 2 }}
              />
            </Box>

            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1, mb: 2 }}>
              {profile.city?.name && <Chip label={profile.city.name} />}
              {profile.district && <Chip label={profile.district} />}
              {profile.inCall && <IconChip icon={<HomeIcon />} label="  " />}
              {profile.outCall && <IconChip icon={<CarIcon />} label="" />}
            </Box>
          </StyledPaper>
        </Grid>

        {/*  */}
        <Grid item xs={12} md={8}>
          <StyledPaper>
            <Typography variant="h6" gutterBottom>
              
            </Typography>
            <ImageList cols={2} gap={16}>
              {profile.photos.map((photo, index) => (
                <ImageListItem key={index}>
                  <ProfileImage
                    src={photo}
                    alt={`${profile.name}  ${index + 1}`}
                    loading="lazy"
                  />
                </ImageListItem>
              ))}
            </ImageList>
          </StyledPaper>
        </Grid>

        {/*    */}
        <Grid item xs={12} md={4}>
          <StyledPaper>
            <Typography variant="h6" gutterBottom>
              
            </Typography>
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1 }}>
              <Typography>: {profile.height} </Typography>
              <Typography>: {profile.weight} </Typography>
              <Typography>: {profile.breastSize}</Typography>
              {profile.nationality && <Typography>: {profile.nationality}</Typography>}
              {profile.hairColor && <Typography> : {profile.hairColor}</Typography>}
            </Box>

            <Divider sx={{ my: 2 }} />

            <Typography variant="h6" gutterBottom>
              
            </Typography>
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1 }}>
              {profile.priceExpress > 0 && (
                <Typography>: {profile.priceExpress}</Typography>
              )}
              <Typography>1 : {profile.price1Hour}</Typography>
              <Typography>2 : {profile.price2Hours}</Typography>
              <Typography>: {profile.priceNight}</Typography>
            </Box>
          </StyledPaper>

          <StyledPaper>
            <Typography variant="h6" gutterBottom>
              
            </Typography>
            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
              {profile.isNonSmoking ? (
                <IconChip icon={<NoSmokingIcon />} label=" " />
              ) : (
                <IconChip icon={<SmokingIcon />} label="" />
              )}
              {profile.is24Hours && <IconChip icon={<TimeIcon />} label="24 " />}
              {profile.isNew && <IconChip icon={<StarIcon />} label="" />}
              {profile.isAlone ? (
                <IconChip icon={<PersonIcon />} label="" />
              ) : (
                <IconChip icon={<GroupIcon />} label=" " />
              )}
            </Box>
          </StyledPaper>
        </Grid>

        {/*  */}
        <Grid item xs={12}>
          <StyledPaper>
            <Typography variant="h6" gutterBottom>
              
            </Typography>
            <Typography variant="body1">
              {profile.description}
            </Typography>
          </StyledPaper>
        </Grid>

        {/*  */}
        <Grid item xs={12}>
          <StyledPaper>
            <Typography variant="h6" gutterBottom>
              
            </Typography>
            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
              {profile.services.map((service, index) => (
                <Chip key={index} label={serviceTranslations[service] || service} />
              ))}
            </Box>
          </StyledPaper>
        </Grid>
      </Grid>
    </Container>
  );
};

export default ProfilePage; 